import Vue from 'vue';
import App from './App.vue';
import vuetify from './plugins/vuetify';

Vue.config.productionTip = false;
Vue.config.devtools =
	location.href.toLowerCase().indexOf('localhost') ||
	location.href.toLowerCase().indexOf('bastest')
		? true
		: false;
new Vue({
	vuetify,
	render: (h) => h(App),
}).$mount('#app');
