<template>
	<div>
		<audio id="myAudio">
			<source src="/confirm.mp3" type="audio/mpeg" />
		</audio>
		<v-container>
			<v-row>
				<v-col cols="12" md="6">
					<v-card elevation="0">
						<v-img>
							<StreamBarcodeReader @decode="onDecode"></StreamBarcodeReader>
						</v-img>
					</v-card>
				</v-col>
				<v-col cols="12" md="6">
					<v-card>
						<v-card-title>Results</v-card-title>
						<v-card-text>
							<v-slide-y-transition mode="out-in">
								<template v-if="detecteds != ''">
									<h3 class="text-h5 text-md-h3" key="scanned">
										{{ detecteds }}
									</h3>
								</template>
							</v-slide-y-transition>
						</v-card-text>
					</v-card>
				</v-col>
			</v-row>
		</v-container>
	</div>
</template>
<script>
	import { StreamBarcodeReader } from 'vue-barcode-reader';

	export default {
		name: 'barcodeEntry',
		data() {
			return {
				detecteds: '',
			};
		},
		components: {
			StreamBarcodeReader,
		},
		methods: {
			logIt(data) {
				var self = this;
				self.detecteds = data;
			},
			onDecode(data) {
				var self = this;
				self.detecteds = '';
				setTimeout(function() {
					self.detecteds = data;
				}, 250);
				document.getElementById('myAudio').play();
			},
			onError(data) {
				var self = this;
				self.detecteds.push(data);
				console.log(data);
			},
		},
	};
</script>
